;(function () {
  /**
   * @description 
   * @param {String} format 
   * @return {String} 
   */
  Date.prototype.toStr = function (format) {
    var o = {
      'M+': this.getMonth() + 1,
      'd+': this.getDate(),
      'h+': this.getHours(),
      'm+': this.getMinutes(),
      's+': this.getSeconds(),
      'q+': Math.floor((this.getMonth() + 3) / 3),
      S: this.getMilliseconds(),
    }
    if (/(y+)/.test(format)) {
      var x = RegExp.$1
      format = format.replace(x, (this.getFullYear() + '').substr(4 - x.length))
    }
    for (var k in o) {
      if (new RegExp('(' + k + ')').test(format)) {
        var x = RegExp.$1
        format = format.replace(
          x,
          x.length == 1 ? o[k] : ('00' + o[k]).substr(('' + o[k]).length)
        )
      }
    }
    return format
  }
  /**
   * @description 
   * @return {Number} 
   */
  Date.prototype.stamp = function () {
    var timestamp = Date.parse(this)
    return timestamp / 1000
  }
  /**
   * @description ()
   * @param {String} endTime 
   * @param {String} time_unit = [day|hours|minutes] 
   * @return {Number} 
   */
  Date.prototype.interval = function (endTime, time_unit) {
    var startTime = this // startTime: 
    var stime = Date.parse(startTime)
    var etime = Date.parse(new Date(endTime))
    var usedTime = etime - stime // 
    if (time_unit === 'day') {
      return Math.floor(usedTime / (1000 * 60 * 60 * 24))
    } else if (time_unit === 'hours') {
      return Math.floor(usedTime / (1000 * 60 * 60))
    } else if (time_unit === 'minutes') {
      return Math.floor(usedTime / (1000 * 60))
    } else {
      return Math.floor(usedTime / 1000)
    }
  }
  /**
   * @description 
   * @param {Number} days 
   * @return {Date} 
   */
  Date.prototype.addDays = function (days) {
    this.setDate(this.getDate() + days)
    return this
  }
  /**
   * @description 
   * @param {Date} seconds 
   */
  Date.prototype.addSeconds = function (seconds) {
    this.setSeconds(this.getSeconds() + seconds)
    return this
  }
})()

